ro__tagline {\n    color: rgb(255, 255, 255);\n    margin-top: 16px;\n    font-size: 18px;\n    font-weight: 500;\n    background-color: #212121;\n}\n\n\n/*\n * main\n */\n\nmain {\n    width: 100%;\n    margin: 0 auto;\n}\n\n\n/*\n * latest\n */\n\n.latest {\n    width: 100%;\n    margin: 60px auto;\n    text-align: center;\n}\n\n.latest__label {\n    font-size: 32px;\n    font-weight: lighter;\n}\n\n.latest__label::after {\n    content: '';\n    margin-top: 16px;\n    display: block;\n    border-bottom: 1px solid #eeeeee;\n}\n\n\n/*\n * footer\n */\n\nfooter {\n    background-color: #2e2e2e;\n    padding: 2em;\n    width: 100%;\n    text-align: center;\n    color: #eeeeee;\n}\n\nfooter ul {\n    margin: 0 auto;\n    display: inline-block;\n}\n\nfooter li {\n    display: inline-block;\n    margin: 0 1em;\n    font-size: 14px;\n}\n\nfooter a {\n    display: inline-block;\n    padding: 1.3em;\n    text-decoration: none;\n    color: #fff\n}\n\n.buttonMore {\n    background-color: #555555;\n    font-size: 14px;\n    padding: 16px;\n    border-radius: 12px;\n    color: #fff;\n    margin: 10px 10px 20px 0px;\n    display: flex;\n}\n\n.buttonMore:hover {\n    background-color: #888888;\n    font-size: 14px;\n    padding: 16px;\n    border-radius: 12px;\n    color: rgb(36, 35, 35);\n}\n\n\n/*Skip to content*/\n\n.link_skip {\n    position: absolute;\n    top: -40px;\n    left: 0;\n    background-color: #ffffff;\n    color: rgb(0, 0, 0);\n    padding: 8px;\n    z-index: 100;\n    text-align: center;\n    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;\n    margin-top: auto;\n    padding-top: 10px;\n    text-decoration: none;\n    font-size: large;\n    min-height: 44px;\n    display: flex;\n}\n\n.link_skip:focus {\n    top: 0;\n}\n\n.link_skip:hover {\n    color: blue;\n}\n\n#hamburger {\n    font-size: 28px;\n    display: none;\n    text-decoration: none;\n}\n\n#drawer {\n    width: 250px;\n    height: 100%;\n    position: absolute;\n    transform: translate(-250px, 0);\n    transition: transform 0.3s ease-in-out;\n    background-color: rgb(61, 61, 61);\n}\n\n#drawer.open {\n    -webkit-transform: translate(0, 0);\n    transform: translate(0, 0);\n}\n\n#drawer ul {\n    list-style-type: none;\n    padding: 0 16px;\n}\n\n#drawer li {\n    padding: 5px 0px;\n    display: flex;\n}\n\n\n/* button/anchor/input text/textarea */\n\nbutton,\na,\ninput,\ntextarea,\nh2,\nh1 {\n    min-width: 44px;\n    min-height: 44px;\n}\n\n\n/*\n  Like\n*/\n\n.like {\n    width: 55px;\n    height: 55px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #db0000;\n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    border-radius: 50%;\n    border: 0;\n    font-size: 18px;\n    color: white;\n    cursor: pointer;\n}\n\n\n/*\n  Movies\n*/\n\n.movies {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 16px;\n    margin: 32px 0;\n}\n\n\n/*\n  Movie Item\n*/\n\n.movie-item {\n    width: 100%;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    overflow: hidden;\n}\n\n.movie-item__not_found {\n    text-transform: uppercase;\n}\n\n.movie-item__header {\n    position: relative;\n}\n\n.movie-item .movie-item__header .movie-item__header__poster {\n    width: 100%;\n}\n\n.movie-item .movie-item__header .movie-item__header__rating {\n    position: absolute;\n    padding: 8px;\n    bottom: 20px;\n    left: 0;\n    display: inline-block;\n    background-color: black;\n    color: white;\n}\n\n.movie-item .movie-item__header .movie-item__header__rating .movie-item__header__rating__score {\n    margin-left: 10px;\n}\n\n.movie-item .movie-item__content {\n    padding: 16px;\n}\n\n.movie-item .movie-item__content h3 {\n    margin: 0 0 10px 0;\n}\n\n.movie-item .movie-item__content h3 a {\n    color: #db0000;\n    text-decoration: none;\n}\n\n.movie-item .movie-item__content p {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: 4;\n    /* number of lines to show */\n    -webkit-box-orient: vertical;\n}\n\n\n/*\n  Movie\n*/\n\n.movie {\n    margin: 0 auto;\n    width: 100%;\n    max-width: 800px;\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 18px 16px;\n}\n\n.movie .movie__poster {\n    width: 100%;\n    max-width: 400px;\n}\n\n.movie .movie__info h4 {\n    margin: 8px 0;\n}"],"sourceRoot":""}]);
